import React from 'react';
import { useState } from 'react';
import './styles.css';
import { Elements } from "@stripe/react-stripe-js"
import { loadStripe } from "@stripe/stripe-js"
import PaymentForm from '../../components/PaymentForm';

const STRIPE_PUBLIC_KEY_TEST = "pk_test_51KiNNxIJsRU3NrL6hjk2WsS42Ww3vbUepszzMxRtkJ8eEGMOd35bIosepRO0RMdYo0iVDq6wDfqNE24lvJQZeyJ500WqdFX1Pu"
const STRIPE_PUBLIC_KEY_PROD = "pk_live_51KiNNxIJsRU3NrL6Gh2yTxyQYnz8xXtY15Hv15d49X0tqNHtKQcjIbo0aJJsCokJ3IshLPXgcT9n5D3v5AJ7RBvM00nrYtJ7MZ"
const stripePromise = loadStripe(STRIPE_PUBLIC_KEY_TEST)

const Payment = () => {
  var emptyCustomer = {
    firstName: null,
    lastName: null,
    email: null,
    confirmedEmail: null,
    streetName: null,
    streetNumber: null,
    postCode: null,
    deliveryNote: null,
    city: 'London',
    country: 'UK'
  }

  const [customer, setCustomer] = useState(emptyCustomer)
  const [customerDetailsAreAdded, setCustomerDetailsAreAdded] = useState(false);
  
  function handleInputChange(e) {
    var newCustomer = customer;
    switch (e.target.id) {
      case 'first-name':
        newCustomer.firstName = e.target.value;
        break;
      case 'last-name':
        newCustomer.lastName = e.target.value;
        break;
      case 'email':
        newCustomer.email = e.target.value;
        break;
      case 'confirm-email':
        newCustomer.confirmedEmail = e.target.value;
        break;
      case 'street-name':
        newCustomer.streetName = e.target.value;
        break;
      case 'street-number':
        newCustomer.streetNumber = e.target.value;
        break;
      case 'post-code':
        newCustomer.postCode = e.target.value;
        break;
      case 'delivery-note':
        newCustomer.deliveryNote = e.target.value;
        break;
      default:
        break;
    }

    if (customer.firstName != null &&
        customer.lastName != null &&
        customer.email != null &&
        customer.confirmedEmail != null &&
        customer.email === customer.confirmedEmail &&
        customer.streetName != null &&
        customer.streetName != null &&
        customer.postCode != null) {

        setCustomerDetailsAreAdded(true)
    }

    setCustomer(newCustomer);
  }

  return (
    <div>
      <div>
        <form className='user-payment-form'>
          <p className='user-payment-form-description'>To whom should we deliver this order?</p>
          <div className='user-payment-form-names'>
            <input type='text' id='first-name' name='first-name' placeholder='First Name' onChange={handleInputChange}></input>
            <input type='text' id='last-name' name='last-name' placeholder='Last Name' onChange={handleInputChange}></input>
          </div>
          <p className='user-payment-form-description'>Where should we send your order info and receipt?</p>
          <div className='user-payment-form-email'>
            <input type='text' id='email' name='email' placeholder='E-mail' onChange={handleInputChange}></input>
            <input type='text' id='confirm-email' name='confirm-email' placeholder='Confirm E-mail' onChange={handleInputChange}></input>
          </div>
          <p className='user-payment-form-description'>Where should we deliver your order?</p>
          <div className='user-payment-form-address'>
            <input className='user-payment-form-address-street-name' type='text' id='street-name' name='street-name' placeholder='Street Name' onChange={handleInputChange}></input>
            <input className='user-payment-form-address-street-number' type='text' id='street-number' name='street-number' placeholder='Street Number' onChange={handleInputChange}></input>
            <input className='user-payment-form-address-post-code' type='text' id='post-code' name='post-code' placeholder='Post Code' onChange={handleInputChange}></input>
            <input className='user-payment-form-address-city' type='text' id='city' name='city' value='London, UK' disabled></input>
          </div>
          <p className='user-payment-form-description'>Delivery Note</p>
          <textarea className='user-payment-form-delivery-note' id='delivery-note' onChange={handleInputChange}></textarea>
        </form>
      </div>
      <Elements stripe={stripePromise}>
        <PaymentForm customer={customer} customerDetailsAreAdded={customerDetailsAreAdded}/>
      </Elements>
    </div>
  );
};

export default Payment;
