import React from 'react';
import axios from 'axios';
import { useState } from 'react';
import { CardElement, useElements, useStripe } from '@stripe/react-stripe-js';
import './styles.css';

<script src="https://js.stripe.com/v3/"></script>

const CARD_OPTIONS = {
    iconStyle: 'solid',
    style: {
        base: {
            iconColor: '#555',
            color: '#111',
            fontWeight: 900,
            fontFamily: 'Montserrat, sans-serif',
            fontSize: '14px',
            fontSmoothing: 'antialiased',
            ':-webkit-autofill': { color: '#555' },
            '::placeholder': { color: '#555' }
        },
        invalid: {
            iconColor: '#f30',
            color: '#f30'
        }
    }
}

const PaymentForm = ({ customer, customerDetailsAreAdded }) => {
    const [success, setSuccess] = useState(false)
    const [serverMessage, setServerMessage] = useState('')
    const stripe = useStripe()
    const elements = useElements()

    const handleSubmit = async (e) => {
        if (customerDetailsAreAdded) {
            setServerMessage('Processing payment...')
            e.preventDefault()

            const {error, paymentMethod} = await stripe.createPaymentMethod({
                type: 'card',
                card: elements.getElement(CardElement)
            })

            if (!error) {
                try {
                    const {id} = paymentMethod
                    const response = await axios.post('https://mokkafoodapi.co.uk:8443/payment', {
                        customerFirstName: customer.firstName,
                        customerLastName: customer.lastName,
                        customerEmail: customer.email,
                        customerStreetName: customer.streetName,
                        customerStreetNumber: customer.streetNumber,
                        customerPostCode: customer.postCode,
                        customerCity: customer.city,
                        customerCountry: customer.country,
                        amount: 100,
                        id: id
                    })

                    setServerMessage(response.data.message)
        
                    if (response.data.success) {
                        console.log('Payment successful.')
                        setSuccess(true)
                    }
                } catch (error) {
                    console.log('Payment error: ', error.message)
                    setServerMessage(error.message)
                }
            } else {
                console.log('Payment error: ', error.message)
                setServerMessage(error.message)
            }
        } else {
            setServerMessage('Customer details are missing.')
            e.preventDefault()
        }
    }

    return (
        <>
        {
            
            !success ?
            <form onSubmit={handleSubmit}>
                <fieldset className='FormGroup'>
                    <div className='FormRow'>
                        <CardElement options={CARD_OPTIONS} />
                    </div>
                </fieldset>
                <button className='payment-button'><i className="fa fa-money-bill-wave"></i>Purchase</button>
                <h3>{serverMessage}</h3>
            </form>
            :
            <div>
                <h3>{serverMessage}</h3>
            </div>
        }
        </>
    )
}
  
export default PaymentForm;