import React from 'react';
import { useState } from 'react';
import Geocode from 'react-geocode'
import './styles.css';

const PostCodePopUp = ({ mealPlanItem, closePostCodePopUp, handleOpenMealPlan, postCodePopUp }) => {
    const [postCode, setPostCode] = useState(null);
    const [erroredPostCode, setErroredPostCode] = useState(null);

    Geocode.setApiKey('AIzaSyDqhSQVRkV4GwtDANDDAMqXIZfD39zqIQ4');
    Geocode.setRegion('UK');

    function handleInputChange(e) {
        if (e.target.value != null && e.target.value.length >= 4) {
            Geocode.fromAddress(e.target.value).then(
                (response) => {
                    const isInLondon = (response.results[0].address_components.find((item) => item.short_name.includes('London')) != null)
                    const isInEssex = (response.results[0].address_components.find((item) => item.short_name === 'Essex') != null)

                    // For debugging:
                    // console.clear()
                    // console.log(response.results[0].address_components)
                    // console.log('isInLondon:' + isInLondon)
                    // console.log('isInEssex:' + isInEssex)

                    if (isInEssex) {
                        const isInEpping = (response.results[0].address_components.find((item) => item.short_name.includes('Epping')) != null)
                        const isInLoughton = (response.results[0].address_components.find((item) => item.short_name.includes('Loughton')) != null)
                        const isInWaltham = (response.results[0].address_components.find((item) => item.short_name.includes('Waltham Abbey')) != null)

                        if (isInEpping || isInLoughton || isInWaltham) {
                            setPostCode(e.target.value)
                        } else {
                            setPostCode('unsupported')
                            setErroredPostCode(e.target.value)
                        }
                    } else {
                        if (isInLondon && 
                            (e.target.value[0].toUpperCase() === 'N' 
                            || e.target.value[0].toUpperCase() === 'E'
                            || (e.target.value[0].toUpperCase() === 'I' && e.target.value[1].toUpperCase() === 'G')
                            || (e.target.value[0].toUpperCase() === 'R' && e.target.value[1].toUpperCase() === 'M'))) {
                            setPostCode(e.target.value);
                        } else {
                            setPostCode('unsupported');
                            setErroredPostCode(e.target.value)
                        }
                    }
                },
                (error) => {
                    // console.error(error)
                    setPostCode('error');
                    setErroredPostCode(e.target.value)
                });
        } else {
            if (e.target.value === '') {
                setPostCode(null);
            }
            return
        }
    }

    function sendUsAnEmail() {
        var codeForEmail = ''

        if (erroredPostCode != null) {
            codeForEmail = erroredPostCode
        }

        window.open('mailto:catering@mokkafood.co.uk?subject=No delivery for post code ' + codeForEmail)
    }

    function closePopUp() {
        setPostCode(null);
        closePostCodePopUp();
    }

    const ResponseMessage = () => {
        if (postCode == null) {
            return null;
        } else if (postCode === 'unsupported') {
            return (
                <div className='pop-up-response-div'>
                    <p className='post-code-sorry'>
                        Oops! Unfortunately, we don't deliver here yet.
                        You could send us an e-mail to request delivery for your location at
                        catering@mokkafood.co.uk!
                    </p>
                    <button className='post-code-sorry-button' onClick={sendUsAnEmail}>
                        <i className='post-code-icon fa-solid fa-envelope'></i> Send us an e-mail
                    </button>
                </div>
            )
        } else if (postCode === 'error') {
            return (
                <div className='pop-up-response-div'>
                    <p className='post-code-sorry'>
                        Oops! Please double-check that the post code is correct.
                    </p>
                    <button className='post-code-sorry-button' onClick={sendUsAnEmail}>
                        <i className='post-code-icon fa-solid fa-envelope'></i> Send us an e-mail
                    </button>
                </div>
            )
        } else {
            return (
                <div className='pop-up-response-div'>
                    <p className='post-code-success'>
                        Good news!<br/>
                        We deliver to this post code!
                    </p>
                    <button className='post-code-success-button' onClick={() => handleOpenMealPlan(mealPlanItem)}>
                        <i className="post-code-icon fa-solid fa-basket-shopping"></i> Customize your plan now!
                    </button>
                </div>
            )
        }
    }

    return (
    postCodePopUp === true
    ?
    <div className='modal-post-code'>
        <div className='modal-content-post-code'>
            <button className='modal-close-button-post-code' onClick={closePopUp}>
                <i className="fas fa-times"></i>
            </button>
            <p className='post-code-question'>
                Could you please enter your post code so we can check if we deliver to you?
            </p>
            <form className='post-code-form'>
                <input className='post-code-text-field' type='text' onChange={handleInputChange} placeholder={ 
                    (postCode === null || postCode === 'error') ? 'Enter Post Code Here' : postCode 
                }></input>
            </form>
            <ResponseMessage />
        </div>
    </div>
    :
    null
    );
};

export default PostCodePopUp;
